<template>
  <main class="content container">
    <common-spinner v-if="productsLoading"></common-spinner>
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        {{ routerCategoryTitle }}
      </h1>
      <span class="content__info">
        {{ cartTotalItemsString }}
      </span>
    </div>
    <div class="content__catalog">
      <product-filter :priceFrom="priceFrom" :priceTo="priceTo" :categoryId="routerCategoryId"
                      :colorId="colorId" @filter="filter" :categories="categories"></product-filter>
      <section class="catalog">
        <div v-if="productsLoadingError">Ошибка при загрузке</div>
        <product-list :products="products"></product-list>
        <base-pagination v-model="currentPage" :perPage="perPage" :count="count" @gotoPage="setPage"></base-pagination>
      </section>
    </div>
  </main>
</template>

<script>

import ProductList from "@/components/ProductList";
import BasePagination from "@/components/BasePagination";
import ProductFilter from "@/components/ProductFilter";
import axios from 'axios';
import {API_BASE_URL} from '@/config';
import CommonSpinner from "@/components/CommonSpinner";
import goodsString from "@/helpers/stringHelper";


export default {
  data: function(){
    return {
      currentPage: 1,
      perPage: 3,
      priceFrom: 0,
      priceTo: 100000,
      categoryAlias: '',
      categoryId: '',
      colorId: 0,
      productsData: null,
      productsLoading: false,
      productsLoadingError: false,
      categoriesData: null,
    }
  },
  components: {
    ProductList, BasePagination, ProductFilter, CommonSpinner
  },
  computed:{
    categories(){
      return this.categoriesData?this.categoriesData.items:[];
    },
    routerCategoryId(){
      return 'id' in this.$route.params ? this.$route.params.id:'';
    },
    routerCategoryTitle(){
      const found = this.categories.find((item)=>item.id===+this.routerCategoryId);
      return this.routerCategoryId && found?
          found.title
          :'Каталог';
    },
    products(){
      return this.productsData?this.productsData.items:[];
    },
    count(){
      return this.productsData?this.productsData.pagination.total:0;
    },
    colors(){
      return new Set(this.products.map(product=>product.color));
    },
    cartTotalItemsString(){
      return goodsString(this.count);
    },
  },
  methods:{
    setPage(value){
      this.currentPage=value;
      this.loadProducts();
    },
    filter(filterData){
      this.priceFrom = filterData.priceFrom;
      this.priceTo = filterData.priceTo;
      this.categoryId = filterData.categoryId?filterData.categoryId:'';
      this.colorId = filterData.colorId;
      this.loadProductsRedirect();
    },
    loadCategories(){
      axios.get( API_BASE_URL+'/api/productCategories')
          .then(response => this.categoriesData = response.data );
    },
    loadProducts(){
      this.productsLoading = true;
      this.productsLoadingError = false;
      axios.get(API_BASE_URL+'/api/products',
          {
            params:{
              page: this.currentPage,
              limit: this.perPage,
              categoryId: this.categoryId,
              minPrice: this.priceFrom,
              maxPrice: this.priceTo,
              colorId: this.colorId
            }
          })
          .then(response => this.productsData = response.data )
          .catch(() => this.productsLoadingError = true )
          .then(() => this.productsLoading = false );
    },
    loadProductsRedirect(){
      this.loadProducts();
      if(this.categoryId!==this.routerCategoryId) {
        this.$router.replace('/' + (this.categoryId ? +this.categoryId : ''));
      }
    }
  },
  watch:{
    '$route.params.id':{
      handler(){ //вместо created
        this.categoryId = this.$route.params.id?this.$route.params.id:0;
        this.loadProducts();
        this.loadCategories();
      },
      immediate: true
    }
  }
}
</script>