<template>
  <main class="content container">
    <common-spinner v-if="isOrderLoading"></common-spinner>
    <div class="content__top">
      <base-breadcrumbs :items="getBreadcrumbs" />

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ this.$route.params.id }}</span>
      </h1>
    </div>

    <section class="cart" v-if="orderInfo">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in orderInfo.basket.items" :key="item.id">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.price | numberFormat }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: {{ cartTotalItemsString }} на сумму <b>{{ orderInfo.totalPrice | numberFormat }} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>

import BaseBreadcrumbs from "@/components/BaseBreadcrumbs";
import numberFormat from "@/helpers/numberFormat";
import goodsString from "@/helpers/stringHelper";
import {mapGetters} from "vuex";
import CommonSpinner from "@/components/CommonSpinner";

export default {
  name: "OrderInfoPage",
  components:{ BaseBreadcrumbs, CommonSpinner },
  computed:{
    ...mapGetters(['isOrderLoading']),
    getBreadcrumbs(){
      return [
        {
          route: {name: 'cart'}
        },
        {
          title: 'Оформление заказа'
        }
      ]
    },
    orderInfo(){
      return this.$store.state.orderInfo??null;
    },
    cartTotalItemsString(){
      return goodsString(this.orderInfo.basket.items.length);
    },
  },
  created() {
    if(this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id)
      return
    this.$store.dispatch('loadOrderInfo', this.$route.params.id)
  },
  filters: {
    numberFormat
  }
}
</script>