<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a href="#" class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled':currentPage===1}" aria-label="Предыдущая страница" @click.prevent="gotoPage(currentPage-1)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="page in pages" :key="page">
      <a href="#" class="pagination__link" :class="{'pagination__link--current': currentPage===page}" @click.prevent="gotoPage(page)">
        {{page}}
      </a>
    </li>
    <li class="pagination__item">
      <a href="#" class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled':currentPage>=pages}" aria-label="Следующая страница" @click.prevent="gotoPage(currentPage+1)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  model:{
    prop: 'currentPage'
  },
  props: ['currentPage', 'perPage', 'count'],
  methods:{
    gotoPage(page){
      if(page>0 && page<=this.pages)
        this.$emit('gotoPage', page)
    }
  },
  computed:{
    pages(){
      return Math.ceil(this.count/this.perPage)
    }
  }
}
</script>