<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="minus()">
      <svg width="10" height="10" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input v-bind="$attrs" type="text" @input="handleInput" v-model="currentValue">

    <button type="button" aria-label="Добавить один товар" @click.prevent="plus()">
      <svg width="10" height="10" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  name: "NumberInput",
  props: ['value'],
  data () {
    return {
      currentValue: this.value
    }
  },
  methods:{
    handleInput() {
      this.$emit('input', this.currentValue)
    },
    minus(){
      if(this.currentValue>1){
        this.currentValue--;
        this.$emit('input', this.currentValue)
      }
    },
    plus(){
      this.currentValue++;
      this.$emit('input', this.currentValue)
    }
  }
}
</script>

