<template>
  <main class="content container">
    <common-spinner v-if="productLoading"></common-spinner>
    <div v-if="productLoadingError">Не удалось загрузить товар</div>
    <div v-if="productData">
      <div class="content__top">
        <base-breadcrumbs :items="getBreadcrumbs" />
      </div>
      <section class="item">
        <div class="item__pics pics">
          <div class="pics__wrapper">
            <img :alt="product.title" height="570" :src="product.image.file.url" width="570">
          </div>
          <ul class="pics__list">
            <li class="pics__item" v-for="image in product.images" :key="image">
              <a class="pics__link pics__link--current" href="">
                <img :alt="product.title" height="98" :src="image" width="98">
              </a>
            </li>
          </ul>
        </div>

        <div class="item__info">
          <span class="item__code">Артикул: {{ product.id }}</span>
          <h2 class="item__title">
            {{ product.title }}
          </h2>
          <div class="item__form">
            <form action="#" class="form" method="POST" @submit.prevent="addToCart">
              <b class="item__price">
                {{ product.price | numberFormat }} ₽
              </b>

              <fieldset class="form__block">
                <legend class="form__legend">Цвет:</legend>
                <ul class="colors">
                  <li class="colors__item" v-for="color in product.colors" :key="color.id">
                    <label class="colors__label">
                      <input class="colors__radio sr-only" name="color-item" type="radio" value="blue">
                      <span class="colors__value" :style="'background-color: '+color.code" :title="color.title">
                        </span>
                    </label>
                  </li>
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend">Объемб в ГБ:</legend>

                <ul class="sizes sizes--primery">
                  <li class="sizes__item">
                    <label class="sizes__label">
                      <input class="sizes__radio sr-only" name="sizes-item" type="radio" value="32">
                      <span class="sizes__value">
                          32gb
                        </span>
                    </label>
                  </li>
                  <li class="sizes__item">
                    <label class="sizes__label">
                      <input class="sizes__radio sr-only" name="sizes-item" type="radio" value="64">
                      <span class="sizes__value">
                          64gb
                        </span>
                    </label>
                  </li>
                  <li class="sizes__item">
                    <label class="sizes__label">
                      <input checked="" class="sizes__radio sr-only" name="sizes-item" type="radio" value="128">
                      <span class="sizes__value">
                          128gb
                        </span>
                    </label>
                  </li>
                </ul>
              </fieldset>

              <div class="item__row">

                <number-input v-model="productAmount"></number-input>

                <button class="button button--primery" type="submit" :disabled="productAdding">
                  В корзину
                </button>
              </div>
              <div v-if="productAdding">Добавляем товар</div>
              <div v-if="productAdded">Товар добавлен в корзину</div>
            </form>
          </div>
        </div>

        <div class="item__desc">
          <ul class="tabs">
            <li class="tabs__item">
              <a class="tabs__link tabs__link--current">
                Описание
              </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#">
                Характеристики
              </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#">
                Гарантия
              </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#">
                Оплата и доставка
              </a>
            </li>
          </ul>

          <div class="item__content">
            <p>
              Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br>
              Синхронизация со смартфоном<br>
              Связь по Bluetooth Smart, ANT+ и Wi-Fi<br>
              Поддержка сторонних приложений<br>
            </p>

            <a href="#">
              Все характеристики
            </a>

            <h3>Что это?</h3>

            <p>
              Wahoo ELEMNT BOLT GPS – это велокомпьютер, который позволяет оптимизировать свои велотренировки, сделав их
              максимально эффективными. Wahoo ELEMNT BOLT GPS синхронизируется с датчиками по ANT+, объединяя полученную с
              них информацию. Данные отображаются на дисплее, а также сохраняются на смартфоне. При этом на мобильное
              устройство можно установить как фирменное приложение, так и различные приложения сторонних разработчиков.
              Велокомпьютер точно отслеживает местоположение, принимая сигнал с целого комплекса спутников. Эта информация
              позволяет смотреть уже преодоленные маршруты и планировать новые велопрогулки.
            </p>

            <h3>Дизайн</h3>

            <p>
              Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры устройства составляют всего 74,6 x 47,3 x 22,1 мм.
              что не превышает габариты смартфона. Корпус гаджета выполнен из черного пластика. На обращенной к пользователю
              стороне расположен дисплей диагональю 56 мм. На дисплей выводятся координаты и скорость, а также полученная со
              смартфона и синхронизированных датчиков информация: интенсивность, скорость вращения педалей, пульс и т.д.
              (датчики не входят в комплект поставки). Корпус велокомпьютера имеет степень защиты от влаги IPX7. Это
              означает, что устройство не боится пыли, а также выдерживает кратковременное (до 30 минут) погружение в воду
              на глубину не более 1 метра.
            </p>
          </div>
        </div>
    </section>
    </div>
  </main>
</template>

<script>

import numberFormat from "@/helpers/numberFormat";
import NumberInput from "@/components/NumberInput";
import axios from "axios";
import {API_BASE_URL} from "@/config";
import CommonSpinner from "@/components/CommonSpinner";
import {mapActions} from 'vuex';
import BaseBreadcrumbs from "@/components/BaseBreadcrumbs";

export default {
  data(){
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingError: false,
      productAdded: false,
      productAdding: false
    }
  },
  components: {NumberInput, CommonSpinner, BaseBreadcrumbs},
  computed: {
    product() {
      return this.productData
    },
    category(){
      return this.productData.category
    },
    getBreadcrumbs(){
      return [
        {
          route: {name: 'catalog', params: {id: this.category.id}},
          title: this.category.title
        },
        {
          title: this.product.title
        }
      ]
    }
  },
  methods: {
    ...mapActions(['addProductToCart']),
    addToCart(){
      this.productAdding = true;
      this.productAdded = false;

      this.addProductToCart({ productId: this.product.id, amount: this.productAmount })
        .then(() => {
          this.productAdding = false;
          this.productAdded = true;
          setTimeout(()=>this.productAdded=false,3000);
        })
    },
    loadProduct(){
      this.productLoading = true;
      this.productLoadingError = false;
      axios.get(API_BASE_URL+`/api/products/${this.$route.params.id}`)
          .then(response => this.productData = response.data )
          .catch(() => this.productLoadingError = true )
          .then(() => this.productLoading = false )
    }
  },
  watch:{
    '$route.params.id':{
      handler(){ //вместо created
        this.loadProduct();
      },
      immediate: true
    }
  },
  filters: {
    numberFormat
  }
}
</script>