<template>
  <ul class="breadcrumbs">
    <li class="breadcrumbs__item">
      <router-link class="breadcrumbs__link" :to="{name: 'main'}">
        Каталог
      </router-link>
    </li>
    <li class="breadcrumbs__item" v-for="(item,key) in items" :key="key">
      <router-link class="breadcrumbs__link" v-if="item.route" :to="item.route">
        {{item.title??routeTitle(item.route.name)}}
      </router-link>
      <span v-else>
        {{ item.title??$route.meta.title }}
      </span>
    </li>
  </ul>
</template>

<script>
export default {
  name: "BaseBreadcrumbs",
  props: ['items'],
  methods:{
    routeTitle(name){
      const found = this.$router.options.routes.find((item) => item.name===name);
      if(found && 'title' in found)
        return found.title;
      return '';
    }
  }
}
</script>