<template>
  <main class="content container">
    <common-spinner v-if="isCartLoading"></common-spinner>
    <div class="content__top">
      <base-breadcrumbs :items="getBreadcrumbs" />
      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info" v-if="cartDetailProducts.length>0">
        {{ cartTotalItemsString }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <cart-item v-for="item in cartDetailProducts" :key="item.productId" :item="item"></cart-item>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ cartTotalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" class="cart__button button button--primery" :to="{name:'order'}">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>

import CartItem from "@/components/CartItem";
import numberFormat from "@/helpers/numberFormat";
import { mapGetters } from 'vuex';
import CommonSpinner from "@/components/CommonSpinner";
import goodsString from "@/helpers/stringHelper"
import BaseBreadcrumbs from "@/components/BaseBreadcrumbs";

export default {
  components: {CartItem, CommonSpinner, BaseBreadcrumbs},
  computed: {
    ...mapGetters(['cartDetailProducts', 'cartTotalPrice','isCartLoading','cartTotalItems']),
    cartTotalItemsString(){
      return goodsString(this.$store.getters.cartTotalItems);
    },
    getBreadcrumbs(){
      return [
        {
        }
      ]
    }
  },
  filters: {
    numberFormat
  }
}
</script>