<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image.file.url" width="120" height="120" srcset="img/phone-square-3@2x.jpg 2x" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">
      Артикул: {{ item.product.id }}
    </span>

    <number-input v-model="amount" class="product__counter"></number-input>

    <b class="product__price">
      {{ item.product.price * item.amount | numberFormat}} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteConfirm(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li >
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import { mapActions } from 'vuex';
import NumberInput from "@/components/NumberInput";

export default {
  name: "CartItem",
  props: ['item'],
  components: {NumberInput},
  filters: {
    numberFormat
  },
  computed:{
    amount:{
      get(){
        return this.item.amount;
      },
      set(value){
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value })
      }
    }
  },
  methods: {
    ...mapActions({deleteProduct: 'deleteCartProduct'}),
    deleteConfirm(productId){
      if(confirm('Удалить товар из корзины?'))
        this.deleteProduct(productId);
    }
  }
}
</script>

