<template>
  <main class="content container">
    <common-spinner v-if="isUploadingOrderInfo"></common-spinner>
    <div class="content__top">
      <base-breadcrumbs :items="getBreadcrumbs" />
      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info" v-if="cartDetailProducts.length>0">
        {{ cartTotalItemsString }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <text-form-field title="ФИО" :error="formError.name" v-model="formData.name"
                           placeholder="Введите ваше полное имя" />

            <text-form-field title="Адрес доставки" :error="formError.address" v-model="formData.address"
                             placeholder="Введите ваш адрес" />

            <text-form-field title="Телефон" :error="formError.phone" v-model="formData.phone"
                             placeholder="Введите ваш телефон" />

            <text-form-field title="Email" :error="formError.email" v-model="formData.email"
                             placeholder="Введи ваш Email" />

            <textarea-form-field title="Комментарий к заказу" :error="formError.comment" v-model="formData.comment"
                             placeholder="Ваши пожелания" />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="product in cartDetailProducts" :key="product.product.id">
              <h3>{{ product.product.title }}</h3>
              <b>{{ product.product.price | numberFormat }} ₽</b>
              <span>Артикул: {{ product.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: {{ cartTotalItemsString }} на сумму <b>{{ cartTotalPrice | numberFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import {mapGetters} from "vuex";
import goodsString from "@/helpers/stringHelper";
import numberFormat from "@/helpers/numberFormat";
import BaseBreadcrumbs from "@/components/BaseBreadcrumbs";
import TextFormField from "@/components/TextFormField";
import TextareaFormField from "@/components/TextareaFormField";
import axios from "axios";
import {API_BASE_URL} from "@/config";
import CommonSpinner from "@/components/CommonSpinner";

export default {
  data(){
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      isUploadingOrderInfo: false
    }
  },
  name: "OrderPage",
  components: {BaseBreadcrumbs, TextFormField, TextareaFormField, CommonSpinner},
  computed: {
    ...mapGetters(['cartDetailProducts', 'cartTotalPrice','isCartLoading','cartTotalItems']),
    cartTotalItemsString(){
      return goodsString(this.cartTotalItems);
    },
    getBreadcrumbs(){
      return [
        {
          route: {name: 'cart'}
        },
        {
          title: 'Оформление заказа'
        }
      ]
    }
  },
  methods:{
    order(){
      this.formError = {};
      this.formErrorMessage = '';

      this.isUploadingOrderInfo = true;
      axios.post(API_BASE_URL+'/api/orders',{
        ...this.formData
      },{
        params:{
          userAccessKey: this.$store.state.userAccessKey
        }
      }).then((response)=>{
        this.$store.commit('resetCart');
        this.$store.commit('updateOrderInfo', response.data );
        this.$router.push({name: 'order-info', params:{ id:response.data.id}});
      })
      .catch((error)=>{
        this.formError = error.response.data.error.request || {};
        this.formErrorMessage = error.response.data.error.message;
      }).then(()=> this.isUploadingOrderInfo = false)
    }
  },
  filters: {
    numberFormat
  }
}
</script>
