<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>
    <form class="filter__form form" action="#" method="get">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="min-price" v-model="localPriceFrom">
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="max-price" v-model="localPriceTo">
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select class="form__select" name="category" v-model="localCategoryId">
            <option value="">Все категории</option>
            <option v-for="category in categories" :key="category.category" :value="category.id">{{
                category.title
              }}
            </option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors">
          <li class="colors__item" v-for="color in colors" :key="color.id">
            <label class="colors__label">
              <input class="colors__radio sr-only" type="radio" name="color" :value="color.id" v-model="localColorId">
              <span class="colors__value" :style="'background-color: '+color.code" :title="color.title" >
                  </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Объемб в ГБ</legend>
        <ul class="check-list">
          <li class="check-list__item">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="8" checked="">
              <span class="check-list__desc">
                    8
                    <span>(313)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="16">
              <span class="check-list__desc">
                    16
                    <span>(461)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="32">
              <span class="check-list__desc">
                    32
                    <span>(313)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="64">
              <span class="check-list__desc">
                    64
                    <span>(313)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="128">
              <span class="check-list__desc">
                    128
                    <span>(313)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="264">
              <span class="check-list__desc">
                    264
                    <span>(313)</span>
                  </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit" @click.prevent="submit">
        Применить
      </button>
      <button class="filter__reset button button--second" type="button" @click.prevent="reset">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>

import axios from "axios";
import {API_BASE_URL} from '@/config';

export default {
  props: [ "priceFrom", "priceTo", "categoryId", "colorId", "categories" ],
  data: function () {
    return {
      localPriceFrom: this.priceFrom,
      localPriceTo: this.priceTo,
      localCategoryId: this.categoryId,
      localColorId: this.colorId,
      localCategories: this.categories,

      colorsData: null,
    }
  },
  computed: {
    colors(){
      return this.colorsData?this.colorsData.items:[];
    }
  },
  methods: {
    submit() {
      let filterData = {
        priceFrom: this.localPriceFrom,
        priceTo: this.localPriceTo,
        categoryId: this.localCategoryId,
        colorId: this.localColorId
      }
      this.$emit('filter', filterData)
    },
    reset() {
      this.localPriceFrom = 0;
      this.localPriceTo = 0;
      this.localCategoryId = 0;
      this.localColorId = 0;
      this.submit();
    },
    loadColors(){
      axios.get( API_BASE_URL+'/api/colors')
          .then(response => this.colorsData = response.data );
    }
  },
  created() {
    this.loadColors();
  },
  watch:{
    '$route.params.id':{
      handler(){ //вместо created
        this.localCategoryId = this.$route.params.id;
      },
      immediate: true
    }
  }
}
</script>

